"use client";
import { useState, useEffect } from "react";
import Loader from "../loader";

export default function MonitorsPage() {
  const [loading, setLoading] = useState(true);
  const [isVisible, setIsVisible] = useState(false);

  useEffect(() => {
    // Simulate loading
    setTimeout(() => {
      setLoading(false);
      setIsVisible(true); // Trigger animations after loading
    }, 6000);
  }, []);

  if (loading) {
    return <Loader />;
  }

  return (
    <div>
      {/* Top Banner */}
      <div className={`bg-blue-600 text-white text-center text-sm py-2 transition-all duration-1000 ${isVisible ? 'opacity-100 translate-y-0' : 'opacity-0 -translate-y-4'}`}>
        Explore the Latest Monitors. <a href="#" className="underline">Shop now &gt;</a>
      </div>

      {/* Monitors Section */}
      <div className="bg-white px-[5%] py-[5%] flex flex-col lg:flex-row items-center justify-between w-full">
        {/* Left Text */}
        <div className={`text-left transition-all duration-1000 delay-600 ${isVisible ? 'opacity-100 translate-y-0' : 'opacity-0 -translate-y-4'}`}>
          <h1 className="font-bold text-black text-[7vw] leading-tight">
            Discover Top Monitors
          </h1>
          <p className="text-[2vw] sm:text-xl text-gray-600 mt-2 mb-[3%]">
            Find the perfect monitor for gaming, work, and entertainment.
          </p>
          <button className="bg-blue-600 mt-[2%] text-white px-[5%] py-[3%] rounded-lg text-[2vw] sm:text-xl font-semibold hover:bg-blue-700 transition-all shadow-md">
            Browse Monitors
          </button>
        </div>

        {/* Right Image */}
        <div className={`lg:w-1/2 flex justify-center relative mt-[3%] lg:mt-0 before:absolute before:content-[''] before:w-[400px] before:h-[400px] before:bg-blue-600 before:blur-[100px] before:opacity-90 before:rounded-full before:top-[60%] before:translate-y-[-70%] transition-all duration-1000 delay-800 ${isVisible ? 'opacity-100 translate-y-0' : 'opacity-0 -translate-y-4'}`}>
          <img src="/images/gaming-pc.png" className="w-[100%] relative" alt="Monitors" />
        </div>
      </div>

      {/* Features */}
      <div className={`flex space-x-8 justify-center items-center mt-12 min-h-[500px] transition-all duration-1000 delay-1200 ${isVisible ? 'opacity-100 translate-y-0' : 'opacity-0 -translate-y-4'}`}>
        <div className="flex flex-col items-center bg-gray-100 rounded-xl p-6 shadow-lg w-100 h-[400px] justify-center">
          <h1 className="text-xl font-semibold mb-2">High Refresh Rates</h1>
          <p className="text-center text-gray-600">Experience smooth visuals with monitors offering refresh rates up to 240Hz.</p>
        </div>
        <div className="flex flex-col items-center bg-gray-100 rounded-xl p-6 shadow-lg w-100 h-[400px] justify-center">
          <h1 className="text-xl font-semibold mb-2">4K Resolution</h1>
          <p className="text-center text-gray-600">Enjoy stunning clarity and detail with 4K Ultra HD monitors.</p>
        </div>
        <div className="flex flex-col items-center bg-gray-100 rounded-xl p-6 shadow-lg w-100 h-[400px] justify-center">
          <h1 className="text-xl font-semibold mb-2">Curved Displays</h1>
          <p className="text-center text-gray-600">Immerse yourself in your content with curved monitor designs.</p>
        </div>
      </div>
    </div>
  );
}